





























import { Component, Prop, Vue } from 'vue-property-decorator';
import { Place } from '@/Dto';
import Tags from '../Tags.vue';

@Component({
  name: 'panel-place-detail',
  components: {
      Tags,
  }
})
export default class PanelPlaceDetail extends Vue {
  @Prop()
  private place!:Place;

  address(place: Place) {
    return place.address.number + ' ' + place.address.street + ', ' + place.address.suburb;
  }
}
